import { Component, OnInit, Input, Output, EventEmitter, ElementRef, Optional, HostBinding } from '@angular/core';
@Component({
  selector: 'farris-tag',
  template: `
    <span [class]="'btn btn-'+ type +' btn-'+ size"
    [class.active]="active"
    [class.disabled]="disabled"
    [class.farris-tag-disabled]="disabled"
    (click)="getValue()" *ngIf="exist">
      <ng-content></ng-content>
      &nbsp;
      <i class="k-icon k-i-close" *ngIf="closable" (click)="closeHandler($event)"></i>
    </span>
  `,
  styleUrls: ['./tag.component.scss']
})
export class FarrisTagComponent implements OnInit {

  /* æ ç­¾å¤§å° */
  @Input()
  get size() {
    if (this.nySize === 'small') {
      return 'sm';
    } else if (this.nySize === 'large') {
      return 'lg'
    } else {
      return 'md'
    }
  }

  set size(size: string) {
    this.nySize = size;
  }

  /* ç±»åæ ·å¼ */
  @Input()
  type = 'secondary';

  /* tagææ¬ */
  @Input()
  text: any;

  /* tagæ è¯ */
  @Input()
  value: any;

  /* ç¦ç¨ */
  @Input()
  disabled: boolean;

  /* æ¯å¦å¯å³é­ */
  @Input()
  closable: boolean;

  /* close emitäºä»¶ */
  @Output()
  close = new EventEmitter<any>();


  @HostBinding('class.farris-tag') tagClass() {
    return true;
  }
  exist = true;
  /* tag æ¯å¦éä¸­ */
  get active() {
    return this.nyActive;
  };

  set active(active: boolean) {
    this.nyActive = active;
  }
  nyActive: boolean;
  nySize: string;
  /* tagå¹¿æ­äºä»¶ */
  @Output()
  selectValue = new EventEmitter<any>();

  constructor(private el: ElementRef) { }

  ngOnInit() {

  }

  getValue() {
    if (this.disabled) {
      return;
    }
    this.nyActive = !this.nyActive;
    this.selectValue.emit({
      value: this.value,
      active: this.active
    });
  }

  hasParent() {
    return this.el.nativeElement.parentElement.classList.contains('farris-tag');
  }

  closeHandler(e: any) {
    e.preventDefault();
    this.exist = false;
    this.close.emit(this.value);
  }

}
